import './Benefits.css';
import squares from '../../images/squares.svg';
import gear from '../../images/gear.svg';
import volume from '../../images/volume.svg';
import time from '../../images/time.svg';

function Benefits() {
    return (
        <section className='benefits'>
            <div className='benefits__item'>
                <img src={squares} className='benefits__item-icon' alt='  ' />
                <p className='benefits__item-description'>  ,   400  4 </p>
            </div>
            <div className='benefits__item'>
                <img src={gear} className='benefits__item-icon' alt='  ' />
                <p className='benefits__item-description'>     </p>
            </div>
            <div className='benefits__item'>
                <img src={volume} className='benefits__item-icon' alt='  ' />
                <p className='benefits__item-description'>     </p>
            </div>
            <div className='benefits__item'>
                <img src={time} className='benefits__item-icon' alt='  ' />
                <p className='benefits__item-description'>   16 </p>
            </div>
        </section>
    )
}

export default Benefits